

.header_Container{

    background-color: blue;
    width :100vw;
    height : auto;
    color :white;
}

.content_Container{

    width:100%;
    background-color: orange;
    display: grid;
    grid-template-columns: 20% 80%;
    padding: 0.78%;
    box-sizing: border-box;
}


.logoContainer{ 
    background-color: green;
    width:100%;
    display: flex;
    justify-content: center;

}

.navigation{
    background-color: purple;
    width:100%;
    display: flex;
    justify-content: right;
}